import { Controller } from '@ali/4ever-cangjie';
import generateRangesFromTableSelection from './generateRangesFromTableSelection';

export const getRangesFromEditor = (controller: Controller) => {
  const { value } = controller;
  const { selection } = value;
  const tableSelection = controller.query('tableSelection');
  const tableRanges = tableSelection ? generateRangesFromTableSelection(controller, [tableSelection]) : undefined;
  const ranges = tableRanges || [selection];
  return ranges;
};
